
import slide from 'components/slide/index'
import http from 'js/http'
import {hidMobile} from 'js/mixin'
import Group from 'vux/src/components/group/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import XDialog from 'vux/src/components/x-dialog/index.vue'

import {mapGetters} from 'vuex'

export default {
name: "index",
components: {
slide,
Group,
CellBox,
XDialog
},
data() {
return {
hidMobile: hidMobile,
wait: 0,
show: false,
vcode: '',
btnLoading: false
}
},
computed: {
codeStr() {
if (!this.wait) {
return '获取验证码'
} else {
return `重新获取(${this.wait}s)`
}
},
...mapGetters([
"loginMsg",
]),
},
methods: {
clickCodeBtn() {
this.wait = 60
let itv = setInterval(() => {
if (this.wait) {
this.wait--
} else {
clearInterval(itv)
}
}, 1000)
http.post('/sms/sms/user-pay-pass', {}, (res) => {
if (res.code !== 0) {
this.$vux.toast.show(res.msg)
}
})
},
submitVcode() {
if (!this.vcode) {
this.$vux.toast.show('请输入您的短信验证码')
return
}
this.btnLoading = true
http.post('/sms/sms/user-validate-vcode', {
vcode:this.vcode
}, (res) => {
this.btnLoading = false
if (res.code !== 0) {
this.$vux.toast.show(res.msg)
return
}
localStorage.setItem("resetVcode", this.vcode);
this.show = false
this.$router.push({
path: '/personal/psw/index/reset'
})
})
}
}
}



